.App {
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  border: 1px solid grey;
  background-color: rgb(250,250,250);
  margin: auto;
  width:80%;

  margin-top: 30px;

}

.Title {
  font-size: 35px;
}

.Button {
  background-color: green;
  border: 1px solid black;
  color: white;
  padding: 10px 24px;
  text-decoration: none;
  font-size: 22px;
}

.Button:focus {
  background-color: blue;
}

.Sum {
  font-size: 36px;
}
.buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}


